import React, { useState } from "react";
import LocationAndCategory from "./tabs/LocationAndCategory";
import TopicContent from "./tabs/TopicContent";
import Media from "./tabs/Media";
import Preview from "./tabs/Preview";
import Header from "../../semantics/header";
import Footer from "../../semantics/footer";
import checkMark from "../../assets/images/check-mark.png";
import "./style.scss";

class TopicSuggestion extends React.Component {
  state = {
    page: 1,
    first: {
      form: {
        category: null,
        location: ""
      },
      error: false,
      page: 1
    }
  };

  firstPageChange = ({ target: { name, value } }) => {
    this.setState(prevState => ({
      ...prevState,
      first: {
        ...prevState.first,
        form: { ...prevState.first.form, [name]: value }
      }
    }));
  };

  onCategoryChange = value => {
    this.setState(prevState => ({
      ...prevState,
      first: {
        ...prevState.first,
        form: { ...prevState.first.form, category: value }
      }
    }));
  };

  moveNextPage = page => {
    const { firstPageForm } = this.state;
    if (page === 1) {
      this.setState({
        page: 2,
        checkMark: true
      });
    } else if (page === 2) {
      this.setState({
        page: 3,
        checkMark: true
      });
    } else if (page === 3) {
      this.setState({
        page: 4,
        checkMark: true
      });
    } else {
      this.setState({
        page: 5,
        checkMark: true
      });
    }
  };
  render() {
    console.log(this.state.first);
    const { category, location } = this.state.first.form;
    return (
      <>
        <Header />
        <div className="suggest-topic">
          <div className="tabs">
            <ul>
              <li>
                Category and <br />
                location
                {/* {this.state.page === 2 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              <li>
                Topic content
                {/* {this.state.page === 3 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              <li>
                Media
                {/* {this.state.page === 4 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              <li>
                Preview
                {/* {this.state.page === 4 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              ew
            </ul>
          </div>
          <div className="tab-content">
            {this.state.page === 1 ? (
              <LocationAndCategory
                moveNextPage={this.moveNextPage}
                onChange={this.firstPageChange}
                onCategoryChange={this.onCategoryChange}
                location={location}
                category={category}
              />
            ) : this.state.page === 2 ? (
              <TopicContent moveNextPage={this.moveNextPage} />
            ) : this.state.page === 3 ? (
              <Media moveNextPage={this.moveNextPage} />
            ) : (
              <Preview moveNextPage={this.moveNextPage} />
            )}
          </div>
        </div>
        <Footer />
      </>
    );
  }
}

export default TopicSuggestion;
